body {
  background-color: #252525;
}

.navbar {
  height: 100px;
  background-color: #252525 !important;
}

.navbar-brand {
  font-size: 60px;
}

a.navbar-brand:hover {
  border-bottom: 2px solid #7f7f7d;
}

.jumbotron {
  background-color: #7f7f7d;
  color: #ffffff;
}

.card {
  background: #252525 !important;
  color: #ffffff;
  border: none;
}

.card img {
  height: 300px;
}

.list-group-item {
  background-color: #7f7f7d !important;
  text-align: left;
}

@media (max-width: 600px) and (min-width: 425px) {
  .card {
    width: 70%;
  }
}

@media (max-width: 424px) and (min-width: 320px) {
  .card {
    width: 90%;
  }
}
